$large-screen: "only screen and (min-width: 1200px)";
$extra-small-screen: "only screen and (max-width: 768px)";

.loading-spinner {
  animation: spin-360 infinite 750ms linear;
  svg {
    fill: white;
  }
}

@keyframes spin-360 {
  from { transform: rotate(360deg); }
  to { transform: rotate(0deg); }
}

@mixin font-mixin() {
  @media #{$large-screen} {
    font-size: 22px;
  }
  @media #{$extra-small-screen} {
    font-size: 15px;
  }
  font-size: 1.8vw;
  font-family: 'Lato', sans-serif;
}

.email-cta-container {
  display: flex;

  .email-signup-field {
    border: solid #2489FF 2px;
    border-radius: 5px;
    height: 37px;
    margin-right: 8px;
    padding: 2px 6px;
    @include font-mixin();
    flex-grow: 3;
  }

  .email-cta-button {
    background-color: #2489FF;
    transition: all ease-in-out 250ms;
    padding: 0 12px;
    height: 45px;
    border: none;
    border-radius: 5px;
    color: white;
    @include font-mixin();
    flex-shrink: 0;

    &:disabled {
      background-color: rgba(104, 124, 165, 1);
    }

    &.success {
      background-color: #15d86c;

      &:hover {
        background-color: #17ee78;
      }
    }

    &:hover {
      background-color: #25a3ff;
    }
  }
}