@import url(https://fonts.googleapis.com/css?family=Lato:300);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.loading-spinner {
  -webkit-animation: spin-360 infinite 750ms linear;
          animation: spin-360 infinite 750ms linear; }
  .loading-spinner svg {
    fill: white; }

@-webkit-keyframes spin-360 {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); } }

@keyframes spin-360 {
  from {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); }
  to {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); } }

.email-cta-container {
  display: -webkit-flex;
  display: flex; }
  .email-cta-container .email-signup-field {
    border: solid #2489FF 2px;
    border-radius: 5px;
    height: 37px;
    margin-right: 8px;
    padding: 2px 6px;
    font-size: 1.8vw;
    font-family: 'Lato', sans-serif;
    -webkit-flex-grow: 3;
            flex-grow: 3; }
    @media only screen and (min-width: 1200px) {
      .email-cta-container .email-signup-field {
        font-size: 22px; } }
    @media only screen and (max-width: 768px) {
      .email-cta-container .email-signup-field {
        font-size: 15px; } }
  .email-cta-container .email-cta-button {
    background-color: #2489FF;
    -webkit-transition: all ease-in-out 250ms;
    transition: all ease-in-out 250ms;
    padding: 0 12px;
    height: 45px;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 1.8vw;
    font-family: 'Lato', sans-serif;
    -webkit-flex-shrink: 0;
            flex-shrink: 0; }
    @media only screen and (min-width: 1200px) {
      .email-cta-container .email-cta-button {
        font-size: 22px; } }
    @media only screen and (max-width: 768px) {
      .email-cta-container .email-cta-button {
        font-size: 15px; } }
    .email-cta-container .email-cta-button:disabled {
      background-color: #687ca5; }
    .email-cta-container .email-cta-button.success {
      background-color: #15d86c; }
      .email-cta-container .email-cta-button.success:hover {
        background-color: #17ee78; }
    .email-cta-container .email-cta-button:hover {
