.landing-page {
  height: 100vh;
  width: 100vw;
  font-family: 'Lato', sans-serif;
  display: grid;
  grid-template-columns: 10% auto 10%;
  grid-template-rows: auto auto;
  grid-template-areas: ". nav-area ." ". cta-area ." "footer footer footer";
  -webkit-align-items: center;
          align-items: center;
  background: url(/static/media/background_mobile.c0fcadc6.svg) center bottom/cover no-repeat; }
  @media only screen and (min-width: 1200px) {
    .landing-page {
      background: url(/static/media/background.271c7847.svg) center right/cover no-repeat; } }
  @media only screen and (max-width: 768px) {
    .landing-page {
      grid-template-columns: 30px auto 30px; } }
  .landing-page .nav-area {
    grid-area: nav-area; }
  .landing-page .information-container {
    grid-area: cta-area;
    display: grid;
    grid-template-columns: 2fr 3fr;
    grid-template-areas: "info product-image";
    grid-column-gap: 5vw;
    -webkit-align-items: center;
            align-items: center; }
    @media only screen and (max-width: 768px) {
      .landing-page .information-container {
        grid-template-columns: 100%;
        grid-template-areas: "info" "product-image";
        text-align: center; } }
    .landing-page .information-container .cta-container {
      grid-area: info; }
    .landing-page .information-container .product-image-container {
      grid-area: product-image; }
      @media only screen and (max-width: 768px) {
        .landing-page .information-container .product-image-container {
          margin-top: 50px; } }
      .landing-page .information-container .product-image-container img {
        max-width: 100%;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15); }
    .landing-page .information-container h2 {
      font-size: 4vw;
      color: #2A2A2A;
      margin-top: 0; }
      @media only screen and (min-width: 1200px) {
        .landing-page .information-container h2 {
          font-size: 45px; } }
      @media only screen and (max-width: 768px) {
        .landing-page .information-container h2 {
          font-size: 25px; } }
    .landing-page .information-container p {
      font-size: 2vw;
      color: #6A6A6A; }
      @media only screen and (min-width: 1200px) {
        .landing-page .information-container p {
          font-size: 25px; } }
      @media only screen and (max-width: 768px) {
        .landing-page .information-container p {
          font-size: 15px; } }

