@import url('https://fonts.googleapis.com/css?family=Lato:300');
$large-screen: "only screen and (min-width: 1200px)";
$extra-small-screen: "only screen and (max-width: 768px)";

.landing-page {
  //background-color: white;
  height: 100vh;
  width: 100vw;
  font-family: 'Lato', sans-serif;

  display: grid;
  grid-template-columns: 10% auto 10%;
  grid-template-rows: auto auto;
  grid-template-areas:
    ". nav-area ."
    ". cta-area ."
    "footer footer footer";
  align-items: center;


  background: url(./background_mobile.svg) center bottom/cover no-repeat ;
  @media #{$large-screen} {
    background: url(./background.svg) center right/cover no-repeat;
  }
  @media #{$extra-small-screen} {
    grid-template-columns: 30px auto 30px;
  }

  .nav-area {
    grid-area: nav-area;
  }

  .information-container {
    grid-area: cta-area;

    display: grid;
    grid-template-columns: 2fr 3fr;
    grid-template-areas: "info product-image";
    grid-column-gap: 5vw;
    align-items: center;

    @media #{$extra-small-screen} {
      grid-template-columns: 100%;
      grid-template-areas:
              "info"
              "product-image";
      text-align: center;
    }

    .cta-container {
      grid-area: info;
    }

    .product-image-container {
      @media #{$extra-small-screen} {
        margin-top: 50px;
      }
      grid-area: product-image;
      img {
        max-width: 100%;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
      }
    }

    h2 {
      font-size: 4vw;
      @media #{$large-screen} {
        font-size: 45px;
      }
      @media #{$extra-small-screen} {
        font-size: 25px;
      }
      color: #2A2A2A;
      margin-top: 0;
    }

    p {
      font-size: 2vw;
      @media #{$large-screen} {
        font-size: 25px;
      }
      @media #{$extra-small-screen} {
        font-size: 15px;
      }
      color: #6A6A6A;
    }
  }
}